
@charset "UTF-8";
@import url("http://res.wx.qq.com/open/libs/weui/1.1.2/weui.min.css");
@import url("http://at.alicdn.com/t/font_b1304fyzu34ygb9.css");
body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea, button, input, select, figure, figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Microsoft Yahei",sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:focus, div:focus, span:focus, header:focus, footer:focus, nav:focus, section:focus, aside:focus, article:focus, ul:focus, dl:focus, dt:focus, dd:focus, li:focus, a:focus, p:focus, h1:focus, h2:focus, h3:focus, h4:focus, h5:focus, h6:focus, i:focus, b:focus, textarea:focus, button:focus, input:focus, select:focus, figure:focus, figcaption:focus {
    outline: none;
}
img {
  max-width: 100%;
  max-height: 100%;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px transparent;
  -webkit-border-radius: 10px;
          border-radius: 10px;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
          border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input[type="button"], input[type="submit"], input[type="search"], input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html, body, #app {
  height: 100%;
  width: 100%;
  background-color: #F5F5F5;
}
body {
  font-size: .12rem;
}
.disFlex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.flex {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.clear:after {
  content: '';
  display: block;
  clear: both;
}
.clear {
  zoom: 1;
}
.back_img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.margin {
  margin: 0 auto;
}
.left {
  float: left;
}
.right {
  float: right;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.gap {
  background: #F5F5F5;
  color: #999;
  font-size: 0.12rem;
  height: 0.28rem;
  line-height: 0.28rem;
  padding: 0 0.1rem;
}
.gap span, .gap i {
    color: #999;
    font-size: 0.14rem;
}
.has-navbar {
  padding-top: 0.5rem;
}
.has-footbar {
  padding-bottom: 0.5rem;
}
.has-sec-navbar {
  padding-top: 0.4rem;
}
.router-slide-enter-active, .router-slide-leave-active {
  -webkit-transition: all .4s;
  transition: all .4s;
}
.router-slide-enter, .router-slide-leave-active {
  -webkit-transform: translate3d(2rem, 0, 0);
          transform: translate3d(2rem, 0, 0);
  opacity: 0;
}
.parent-container {
  position: absolute;
  height: 100%;
  right: 0;
  left: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  overflow-y: hidden;
}
.second-container {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
header.desc-head {
  position: relative;
}
header.desc-head h4, header.desc-head h3 {
    color: #fff;
    font-size: .16rem;
}
header.desc-head h3 {
    font-size: .14rem;
}
header.desc-head p {
    color: #eee;
    font-size: .12rem;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}
header.desc-head .weui-media-box {
    padding: .08rem;
    width: 100%;
    -webkit-align-self: center;
        -ms-flex-item-align: center;
                -ms-grid-row-align: center;
            align-self: center;
}
header.desc-head .weui-media-box_appmsg:before {
    display: none;
}
header.desc-head .weui-media-box_appmsg .weui-media-box__hd {
    width: 0.8rem;
    height: 0.8rem;
}
header.desc-head section .icon-section {
    color: #999;
}
.blur-background {
  background-position: center center;
  -webkit-filter: blur(0.6rem) brightness(0.6);
          filter: blur(0.6rem) brightness(0.6);
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 100%;
}
.weui-btn_primary {
  background-color: #e82a2a;
}



